  handleCommandKey(event) {
    if (event.key === 'ArrowUp') {
      document.getElementById('textBox').value = this.lastState.value;
      this.state.value = this.lastState.value;
    }
  }

  handleCommandLine(event) {
    this.setState({value: event.target.value});
  }

